@font-face {
  font-family: 'Roboto-Bold';
  src: local('Roboto'), url(./../../fonts/Roboto-Bold.ttf) format('truetype');
}
$lineColor: #EEEEEE;
.react-tabs {
  -webkit-tap-highlight-color: transparent;
  margin-top: 96px;
  margin-left: 128px;

  &__tab-list {
    border-bottom: 1px solid $lineColor;
    border-top: 1px solid $lineColor;
    margin: 0 0 10px;
    padding: 0;
    
    
  }

  &__tab {
    display: inline-block;
    // border: 1px solid transparent;
    border-bottom: none;
    bottom: -1px;
    position: relative;
    list-style: none;
    padding: 6px 12px;
    cursor: pointer;

    &--selected {
      background: #fff;
      border-color: #aaa;
      color: rgb(0, 0, 0);
      border-radius: 5px 5px 0 0;
    }

    &--disabled {
      color: GrayText;
      cursor: default;
    }

    &:focus {
      box-shadow: 0 0 5px hsl(208, 99%, 50%);
      border-color: hsl(208, 99%, 50%);
      outline: none;

      &:after {
        content: "";
        position: absolute;
        height: 5px;
        left: -4px;
        right: -4px;
        bottom: -5px;
        background: #fff;
      }
    }
  }

  &__tab-panel {
    display: none;

    &--selected {
      display: block;
    }
  }
}
